import React, { useEffect, useState, useCallback, useRef } from 'react';

import { Link } from "react-router-dom"
import ButtonBottom from "./components/ButtonBottom"
import * as Element from "./styles"

import logoImg from "../../assets/logo.svg"
import zombie from '../../assets/zombie.svg'

import { useToast } from '../../hooks/toast';
import * as Yup from 'yup';
import api from '../../services/api';

import 'react-responsive-modal/styles.css';
import { Modal } from 'react-responsive-modal';

import { ModalButton, InputLabel} from '../../components/Form';

import { Form } from './styles';
import getValidationErrors from '../../utils/getValidationErrors';

const Landing = () => {
  const formRef = useRef(null);
  const [movie, setMovie] = useState([]);
  const [submitIsLoading, setSubmitIsLoading] = useState(false);

  const { addToast } = useToast();

  const [open, setOpen] = useState(false);

  const onOpenModal = () => setOpen(true);
  const onCloseModal = () => setOpen(false);

  const handleButtonSubmit = useCallback(() => {
    formRef.current.submitForm();
  }, []);

  const handleSubmit = useCallback(
    async (data) => {


      try {
        formRef.current.setErrors({});

        const formSchema = Yup.object().shape({
          name: Yup.string().required('Campo obrigatório'),
          email: Yup.string().email('Email incorreto').required('Campo obrigatório')
        });

        await formSchema.validate(data, { abortEarly: false })

        const { name, email } = data;

        setSubmitIsLoading(true);

        await api
          .post('/leads', { name, email })
          .then(response => {
            formRef.current.reset();
            onCloseModal()
            addToast({
              title: 'Agradecemos por compartilhar seus dados conosco. Em breve, nossa equipe entrará em contato!',
              type: 'success',
            });
          })
          .catch(error => {
            const { bcode } = error.response.data

            onCloseModal()

            if (bcode === 1002) {
              addToast({
                title: 'Agradecemos por compartilhar seus dados conosco. Em breve, nossa equipe entrará em contato!',
                type: "success"
              })
            } else {
              console.log(error)
              addToast({
                title: `Ocorreu um erro desconhecido. Veja o console do navegador :(`,
                type: "error"
              })
            }
          })

      } catch (err) {
        if (err instanceof Yup.ValidationError) {
          const errors = getValidationErrors(err);

          formRef.current.setErrors(errors);
        }
        console.log(err)
      } finally {
        setSubmitIsLoading(false);
      }
    },
    [addToast],
  );


  const loadMovie = useCallback(async () => {
    try {
      const response = await api.get('/catalog');
      setMovie(response.data.data);
    }
    catch (err) {
      addToast({
        type: 'error',
        title: 'Erro na busca',
      });
    }
  }, [addToast]);

  useEffect(() => {
    loadMovie();
  }, [addToast, loadMovie]);


  return (
    <Element.Container>
      <Element.Main>
        <Element.Banner id="leads" />

        <Modal open={open} onClose={onCloseModal} center classNames={{
          overlay: 'customOverlay',
          modal: 'customModal',
        }}>
          <h2>Fila de espera</h2>
          <div>
            <p>Faça o pré-cadastro e receba uma oferta especial na semana do lançamento.</p>
            <Form ref={formRef} onSubmit={handleSubmit}>
              <div className="fields">
                <InputLabel name="name" label_name="name" placeholder="Informe seu nome" />
                <InputLabel name="email" label_name="email" placeholder="Informe seu email" />

                <ModalButton isLoading={submitIsLoading} onClick={handleButtonSubmit} />
              </div>
            </Form>
          </div>
        </Modal>


        <Element.Hero>
          <Element.SubTitle>
            Mais que um streaming, uma experiência arrepiante!
          </Element.SubTitle>
          <Element.Image src={logoImg} alt="Zombie+" title="Zombie+" />
          <Element.HeroButton className="full purple" onClick={onOpenModal}>
            Aperte o play... se tiver coragem
          </Element.HeroButton>
          <span>*Faça o pré-cadastro e receba uma oferta especial no lançamento.</span>
          <Link to="/sing-up" className="link">
            Em breve por apenas:
          </Link>
          <p className="price">R$ 27,90/mês* ou R$ 279,90/ano à vista*</p>
        </Element.Hero>

        <Element.Devices>
          <div>
            <h2>Prepare a pipoca e apague as luzes.</h2>
            <p>
              No mundo dos mortos-vivos, cada história é uma jornada de
              sobrevivência, esperança e terror. No Zombie+, trazemos todos
              esses mundos apocalípticos diretamente para a sua tela. Pronto
              para enfrentar o fim?
            </p>
          </div>
          <img src="/assets/images/devices.png" alt="" />
        </Element.Devices>

        <Element.Feature>
          <h2>Destaques</h2>
          <p>
            Só no Zombie+ você vê os melhores filmes, séries, curtas e outros
            conteúdos apocalípticos.
          </p>
          <div>
            {movie.map(movie => (
              <img key={movie.id} src={
                movie.cover ?
                  `http://localhost:3333/uploads/${movie.cover}` :
                  zombie
              } alt={movie.title} />
            ))}

          </div>
        </Element.Feature>

        <Element.Device>
          <h2>Disponível nos seus dispositivos favoritos</h2>

          <Element.DeviceList>
            <div>
              <img src="/assets/images/tv.png" alt="" />
              <h3>TV</h3>
              <p>Amazon Fire TV</p>
              <p>Android TV</p>
              <p>Apple TV</p>
              <p>Chromecast</p>
              <p>TVs LG</p>
              <p>Roku</p>
              <p>Samsung</p>
            </div>
            <div>
              <img src="/assets/images/computer.png" alt="" />
              <h3>Computador</h3>
              <p>Chrome OS</p>
              <p>MacOS</p>
              <p>PC Windows</p>
            </div>
            <div>
              <img src="/assets/images/tablet.png" alt="" />
              <h3>Celulares e tablets</h3>
              <p>Tablets Amazon Fire</p>
              <p>Celulares e tablets Android</p>
              <p>iPhone e iPad</p>
            </div>

            <div>
              <img src="/assets/images/consoles.png" alt="" />
              <h3>Videogames</h3>
              <p>PS4</p>
              <p>PS5</p>
              <p>Xbox One</p>
              <p>Xbox Series X</p>
              <p>Xbox Series S</p>
            </div>
          </Element.DeviceList>
        </Element.Device>

        <ButtonBottom />
      </Element.Main>

      <Element.Footer id="footer">
        <a href="/"><img src={logoImg} alt="Logo Zombie+" /></a>

        <Element.Flex>
          <p><a href="/">Sobre Zombie+</a></p>
          <p><a href="/">Centro de Assinatura</a></p>
          <p><a href="/">Política de privacidade</a></p>
          <p><a href="/">Termos de uso</a></p>
          <p><a href="/">Dispositivos compatíveis</a></p>
          <p><a href="/">Ajuda</a></p>
          <p><a href="/admin">Admin</a></p>
        </Element.Flex>
        <Element.Flex>
          <p>
            ® 2023 Zombie+ é um sistema exclusivo para treinamentos do Papito e
            da QAxperience. Todos os direitos reservados.
          </p>
        </Element.Flex>
      </Element.Footer>
    </Element.Container>
  )
}

export default Landing
