import React from "react"
import { Switch, Redirect } from "react-router-dom"

import SignIn from "../pages/SignIn"
import Leads from "../pages/Leads"

import Movies from "../pages/Movies"
import CreateMovie from "../pages/Movies/Create"

import TvShows from "../pages/TvShows"
import CreateTvShow from "../pages/TvShows/Create"

import Landing from "../pages/Landing"

import Route from "./Route"

const Routes = () => {
  return (
    <Switch>
      <Route path="/" exact component={Landing} />
      <Route path="/admin" exact component={() => <Redirect to="/admin/login" />} />
      
      <Route path="/logout" exact component={() => <Redirect to="/" />} />
      <Route path="/admin/login" exact component={SignIn} />

      <Route path="/admin/leads" exact component={Leads} isPrivate />

      <Route path="/admin/movies" exact component={Movies} isPrivate />
      <Route path="/admin/movies/register" exact component={CreateMovie} isPrivate />

      <Route path="/admin/tvshows" exact component={TvShows} isPrivate />
      <Route path="/admin/tvshows/register" exact component={CreateTvShow} isPrivate />
    </Switch>
  )
}

export default Routes
