import React, { useCallback, useRef, useState } from 'react';
import { useHistory } from 'react-router-dom';

import * as Yup from 'yup';
import api from '../../../services/api';

import { BackButton, RegisterButton, InputFile, InputLabel, TextArea } from '../../../components/Form';

import CompanySelect from '../../../components/CompanySelect';
import YearSelect from '../../../components/YearSelect';
import { useToast } from '../../../hooks/toast';

import getValidationErrors from '../../../utils/getValidationErrors';

import { Container, Content, Form } from './styles';

import Switch from "react-switch";

const CreateMovie = () => {
  const formRef = useRef(null);
  const { addToast } = useToast();
  const [submitIsLoading, setSubmitIsLoading] = useState(false);

  const [featured, setFeatured] = useState(false);
  const [cover, setCoverFile] = useState();

  const history = useHistory();

  const handleSetFeatured = (() => {
    featured ? setFeatured(false) : setFeatured(true);
  });

  const handleButtonSubmit = useCallback(() => {
    formRef.current.submitForm();
  }, []);

  const handleSubmit = useCallback(
    async (data) => {

      try {
        formRef.current.setErrors({});

        console.log({ ...data, cover: cover })

        const formSchema = Yup.object().shape({
          title: Yup.string().required('Por favor, informe o título.'),
          overview: Yup.string().required('Por favor, informe a sinopse.'),
          select_year: Yup.string().required('Por favor, informe o ano de lançamento.'),
          select_company_id: Yup.string().required('Por favor, informe a empresa distribuidora.'),
        });

        await formSchema.validate(data, { abortEarly: false })

        const formData = new FormData()
        const { title, overview, select_company_id, select_year } = data;

        formData.append("cover", cover)
        formData.append("title", title)
        formData.append("overview", overview)
        formData.append("company_id", select_company_id)
        formData.append("release_year", select_year)
        formData.append("featured", featured ? true : false)

        setSubmitIsLoading(true);

        await api
          .post('movies', formData)
          .then(response => {
            formRef.current.reset();

            addToast({
              title: 'Uhull',
              description: 'Cadastro realizado com sucesso!',
              type: 'success',
            });

            history.push('/admin/movies');
          })
          .catch(error => {
            const { bcode } = error.response.data

            if (bcode === 1001) {
              addToast({
                title: "Oops!",
                description: 'Este conteúdo já encontra-se cadastrado no catálogo',
                type: "error"
              })
            } else {
              console.log(error)
              addToast({
                title: "Vixe!",
                description: `Ocorreu um erro desconhecido. Veja o console do navegador :(`,
                type: "error"
              })
            }
          })

      } catch (err) {
        if (err instanceof Yup.ValidationError) {
          const errors = getValidationErrors(err);

          formRef.current.setErrors(errors);
        }
        console.log(err)
      } finally {
        setSubmitIsLoading(false);
      }
    },
    [featured, cover, addToast, history],
  );

  return (
    <Container>
      <Content>
        <header>
          <h1>Cadastrar novo Filme</h1>

          <section>
            <BackButton />

            <RegisterButton isLoading={submitIsLoading} onClick={handleButtonSubmit} />
          </section>
        </header>

        <Form ref={formRef} onSubmit={handleSubmit}>
          <div className="fields">
            <InputLabel name="title" label_name="title" label="Titulo do filme" placeholder="Digite aqui" />
            <TextArea name="overview" label_name="overview" label="Sinopse" placeholder="Digite aqui" />
            <div className="column">
              <CompanySelect />
              <YearSelect />
            </div>

            <div className="form-group">
              <InputFile name="cover" label_name="cover" label="Poster" onChange={(e) => setCoverFile(e.target.files[0])} />

              <label className="featured field-name">
                <span>Conteúdo destaque?</span>
                <Switch onChange={handleSetFeatured} checked={featured} onColor="#3a295f" onHandleColor="#6139b6" handleDiameter={30} uncheckedIcon={false} checkedIcon={false} boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)" activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)" height={20} width={48} className="react-switch" />
              </label>
            </div>

          </div>
        </Form>
      </Content>
    </Container>
  )
};

export default CreateMovie;
