import React from 'react';
import { FiSave } from 'react-icons/fi';

import { ButtonContainer } from './styles';

const ModalButton = ({ onClick, isLoading }) => {
  return (<ButtonContainer type="button" onClick={onClick} disabled={isLoading}>
    <FiSave size={24} />
    {isLoading ? 'Aguarde ...' : 'Quero entrar na fila!'}
  </ButtonContainer>);
};

export default ModalButton;
