import React, { useRef, useEffect } from "react"
import Select from "react-select/async"
import { useField } from "@unform/core"

import { LabelContainer } from "./styles"

const InputAsyncSelect = ({
  name,
  label,
  label_name,
  className,
  showErro = "bottom",
  ...rest
}) => {
  const selectRef = useRef(null)
  const { fieldName, defaultValue, registerField, error } = useField(name)

  const colourStyles = {
    control: styles => ({
      ...styles,
      border: 0,
      marginTop: 8,
      borderRadius: 10,
      borderColor: "#202024",
      fontSize: 18,
      height: 56,
      outline: "#202024"
    }),
    option: styels => ({
      ...styels,
      color: "#F4EDE8"
    })
  }

  const themeProps = theme => {
    return {
      ...theme,
      colors: {
        ...theme.colors,
        primary: "#6139b6",
        primary25: "#6139b6",
        primary50: "#4d2b96",
        pink: "#121214",
        pink25: "#6139b6",
        pink50: "#0a0c14",
        pink75: "#6139b6",
        danger: "#de350b",
        dangerLight: "#ffbdad",
        neutral0: "#121214",
        neutral5: "#6139b6",
        neutral10: "#e6e6e6",
        neutral20: "#cccccc",
        neutral30: "#6139b6",
        neutral40: "#999999",
        neutral50: "#808080",
        neutral60: "#666666",
        neutral70: "#4d4d4d",
        neutral80: "#F4EDE8",
        neutral90: "#1a1a1a"
      }
    }
  }

  useEffect(() => {
    registerField({
      name: fieldName,
      ref: selectRef.current,
      getValue: ref => {
        if (rest.isMulti) {
          if (!ref.select.state.value) {
            return []
          }

          return ref.select.state.value.map(option => option.value)
        }
        if (!ref.select.state.value) {
          return ""
        }

        return ref.select.state.value.value
      },
      setValue: (ref, value) => {
        ref.select.state.value = value
      },
      clearValue: ref => {
        ref.select.select.clearValue()
      }
    })
  }, [fieldName, registerField, rest.isMulti])

  return (
    <LabelContainer
      showErro={showErro}
      hasError={!!error}
      htmlFor={label_name || name}
      className={className}
    >
      <span className="field-name">{label}</span>
      <Select
        cacheOptions
        defaultValue={defaultValue}
        ref={selectRef}
        classNamePrefix="react-select"
        theme={themeProps}
        maxMenuHeight={225}
        styles={colourStyles}
        name={label_name || name}
        id={label_name || name}
        loadingMessage={() => "Carregando ..."}
        {...rest}
      />
      {error && showErro === "bottom" && <span className="alert">{error}</span>}
    </LabelContainer>
  )
}

export default InputAsyncSelect
