import React, { useCallback, useRef, useState } from "react"
import * as Yup from "yup"
import { FiMail, FiLock } from "react-icons/fi"
import { Form } from "@unform/web"
import { useHistory } from "react-router-dom"

import logoImg from "../../assets/logo.svg"

import { useAuth } from "../../hooks/auth"
import { useToast } from "../../hooks/toast"
import { UInput } from "../../components/Form"

import getValidationErrors from "../../utils/getValidationErrors"

import { Container, SubmitButton } from "./styles"

const SignIn = () => {
  const [isLoading, setIsLoading] = useState(false)
  const formRef = useRef(null)

  const { signIn } = useAuth()
  const history = useHistory()
  const { addToast } = useToast()

  const handleSubmit = useCallback(
    async ({ email, password }) => {
      try {
        formRef.current.setErrors({})

        const schema = Yup.object().shape({
          email: Yup.string().email('Email incorreto').required('Campo obrigatório'),
          password: Yup.string().required('Campo obrigatório')
        })

        await schema.validate({ email, password }, { abortEarly: false })

        setIsLoading(true)
        await signIn({ email, password })

        history.push("/admin")
      } catch (err) {
        if (err instanceof Yup.ValidationError) {
          const errors = getValidationErrors(err)

          formRef.current.setErrors(errors)
        } else {
          addToast({
            title: "Oops!",
            description: 'Ocorreu um erro ao tentar efetuar o login. Por favor, verifique suas credenciais e tente novamente.',
            type: "error"
          })
        }



      } finally {
        setIsLoading(false)
      }
    },
    [history, signIn, addToast]
  )

  return (
    <Container>
      <div className="login-form">
        <img src={logoImg} alt="Logo" />
        <Form ref={formRef} onSubmit={handleSubmit}>

          <UInput
            icon={FiMail}
            name="email"
            placeholder="E-mail"
            type="text"
            autoFocus
            disabled={isLoading}
          />
          <UInput
            icon={FiLock}
            name="password"
            placeholder="Senha"
            type="password"
            disabled={isLoading}
          />
          <SubmitButton disabled={isLoading} type="submit">
            {isLoading ? "Carregando..." : "Entrar"}
          </SubmitButton>
        </Form>
      </div>
    </Container>
  )
}

export default SignIn
