import React, { useEffect, useState, useCallback } from 'react';
import { Ring } from 'react-awesome-spinners';
import { useQueryParam, StringParam } from 'use-query-params';

import { FiTrash } from 'react-icons/fi';
import callie from '../../assets/callie.svg'

import api from '../../services/api';
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';

import { useToast } from '../../hooks/toast';

import Header from '../../components/Header';

import * as S from './styles';

const ListMovie = () => {
  const [movies, setMovies] = useState([]);
  const [loading, setLoading] = useState(true);
  const [queryTitle, setQueryTitle] = useQueryParam('name', StringParam);
  const { addToast } = useToast();

  const handleSearchSubmit = useCallback(({ name }) => {
    setQueryTitle(name || undefined);
  }, [setQueryTitle]);

  const loadMovie = useCallback(async () => {
    try {
      setLoading(true);
      const response = await api.get('/movies', {
        params: {
          title: queryTitle || undefined,
        },
      });
      setMovies(response.data.data);
    }
    catch (err) {
      addToast({
        type: 'error',
        title: 'Vixe!',
        description: 'Ocorreu um erro ao tentar buscar registros'
      });
    }
    finally {
      setLoading(false);
    }
  }, [addToast, queryTitle]);

  const handleDelete = async (movieId) => {
    api.delete(`/movies/${movieId}`)
      .then(async (response) => {
        loadMovie();
        addToast({
          type: 'success',
          title: 'Tudo certo!',
          description: 'Item removido com sucesso'
        });
      });
  };

  useEffect(() => {
    loadMovie();
  }, [addToast, queryTitle, loadMovie]);

  return (<S.Container>
    <S.Content>
      <Header initialName={queryTitle} onSubmit={handleSearchSubmit} createPage="/admin/movies/register" title="Filmes" placeholder="Busque pelo nome" />

      {movies.length === 0 && !loading &&
        <S.MessageContainer>
          <span>Nenhum registro encontrado!</span>
        </S.MessageContainer>}

      {movies.length > 0 && !loading &&
          <S.Table>
            <tbody>
              {movies.map(movie => (
                <S.MovieRow key={movie.id}>
                  <td>
                    <img src={
                      movie.cover ?
                        `http://localhost:3333/uploads/${movie.cover}` :
                        callie
                    } alt={movie.title} />
                  </td>
                  <td className="title">
                    {movie.title}
                  </td>
                  <td>
                    {movie.overview}
                  </td>
                  <td>
                    <small>Distribuição:</small>
                    {movie.company.name}
                  </td>
                  <td>
                    <small>Lançamento:</small>
                    {movie.release_year}
                  </td>
                  <td className='remove-item'>
                    <Popup
                      trigger={
                        <button type="button" className="button request-removal">
                          <FiTrash size={24} />
                        </button>
                      }
                      position={['bottom center']}
                    >
                      <div className="tooltip">Clique <span className="confirm-removal" onClick={() => handleDelete(movie.id)}>aqui</span> para confirmar a exclusão!</div>
                    </Popup>
                  </td>
                </S.MovieRow>))}
            </tbody>
          </S.Table>
      }

      {loading &&
        <S.MessageContainer>
          <Ring size={100} color="#6421ff" />
        </S.MessageContainer>}
    </S.Content>
  </S.Container>);
};

export default ListMovie;
