import React, { useEffect, useState, useCallback, useMemo } from "react"

import api from "../../services/api"

import { Group } from "./styles"
import { InputAsyncSelect } from "../Form"

const CompanySelect = () => {
  const [companies, setCompanies] = useState([])
  const [optionsIsLoading, setOptionsIsLoading] = useState(true)

  useEffect(() => {
    async function loadCompanies() {
      const response = await api.get("/companies")
      setCompanies(response.data.data)
      setOptionsIsLoading(false)
    }

    loadCompanies()
  }, [])

  const companyOptions = useMemo(() => {
    return companies.map(company => ({
      value: company.id,
      label: company.name
    }))
  }, [companies])

  const handleLoadCompaniesOptions = useCallback(
    async (inputValue, callback) => {
      const data = companyOptions.filter(company =>
        company.label.includes(inputValue)
      )

      if (data.length === 0) {
        setOptionsIsLoading(true)
        const response = await api.get("/companies", {
          params: {
            name: inputValue
          }
        })

        setOptionsIsLoading(false)

        console.log(response)

        callback(
          response.data.map(company => ({
            label: company.name,
            value: company.id
          }))
        )
        return
      }

      callback(data)
    },
    [companyOptions]
  )

  return (
    <Group>
      <InputAsyncSelect
        name="select_company_id"
        label="Distribuido por:"
        placeholder="Selecione..."
        noOptionsMessage={() => "Nenhum registro encontrado"}
        defaultOptions={companyOptions}
        loadOptions={handleLoadCompaniesOptions}
        isLoading={optionsIsLoading}
      />
    </Group>
  )
}

export default CompanySelect
