import React, { createContext, useCallback, useState, useContext } from "react"

import api from "../services/api"

const AuthContext = createContext({})

const AuthProvider = ({ children }) => {
  const [data, setData] = useState(() => {
    const token = sessionStorage.getItem("@ZombiePlus:token")
    const user = sessionStorage.getItem("@ZombiePlus:user")

    if (token && user) {
      api.defaults.headers.Authorization = `Bearer ${token}`
      return { token, user: JSON.parse(user) }
    }

    return {}
  })
  // const [data, setData] = useState<IAuthState>({} as IAuthState);

  const signIn = useCallback(async ({ email, password }) => {
    const response = await api.post("sessions", { email, password })

    const { token, user } = response.data

    api.defaults.headers.Authorization = `Bearer ${token}`

    sessionStorage.setItem("@ZombiePlus:token", token)
    sessionStorage.setItem("@ZombiePlus:user", JSON.stringify(user))

    setData({ token, user })
  }, [])

  const signOut = useCallback(() => {
    sessionStorage.removeItem("@ZombiePlus:token")
    sessionStorage.removeItem("@ZombiePlus:user")

    setData({})
  }, [])

  return (
    <AuthContext.Provider value={{ user: data.user, signIn, signOut }}>
      {children}
    </AuthContext.Provider>
  )
}

function useAuth() {
  const context = useContext(AuthContext)

  if (!context) {
    throw new Error("useAuth must be used within an AuthProvider")
  }

  return context
}

export { AuthProvider, useAuth }
