import React, { useEffect, useState, useCallback } from 'react';
import { Ring } from 'react-awesome-spinners';
import { useQueryParam, StringParam } from 'use-query-params';

import { FiTrash } from 'react-icons/fi';

import api from '../../services/api';

import { useToast } from '../../hooks/toast';
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';

import Header from '../../components/Header';

import * as S from './styles';

const ListLead = () => {
  const [lead, setLead] = useState([]);
  const [loading, setLoading] = useState(true);
  const [queryTitle, setQueryTitle] = useQueryParam('name', StringParam);
  const { addToast } = useToast();

  const handleSearchSubmit = useCallback(({ name }) => {
    setQueryTitle(name || undefined);
  }, [setQueryTitle]);

  const loadLead = useCallback(async () => {
    try {
      setLoading(true);
      const response = await api.get('/leads', {
        params: {
          email: queryTitle || undefined,
        },
      });
      setLead(response.data.data);
    }
    catch (err) {
      addToast({
        type: 'error',
        title: 'Vixe!',
        description: 'Ocorreu um erro ao tentar buscar leads'
      });
    }
    finally {
      setLoading(false);
    }
  }, [addToast, queryTitle]);

  const handleDelete = async (leadId) => {
    api.delete(`/leads/${leadId}`)
      .then(async (response) => {
        loadLead();
        addToast({
          type: 'success',
          title: 'Uhull!',
          description: 'Lead removido com sucesso'
        });
      });
  };

  useEffect(() => {
    loadLead();
  }, [addToast, queryTitle, loadLead]);

  return (<S.Container>
    <S.Content>
      <Header initialName={queryTitle} onSubmit={handleSearchSubmit} title="Gestão de leads" placeholder="Busque pelo email" />

      {lead.length === 0 && !loading &&
        <S.MessageContainer>
          <span>Nenhum lead encontrado!</span>
        </S.MessageContainer>}



      {lead.length > 0 && !loading &&
        <S.Table>
          <tbody>
            {lead.map(lead => (
              <S.LeadRow key={lead.id}>
                <td className="name">
                  <small>Nome:</small>
                  {lead.name}
                </td>
                <td>
                  <small>Email:</small>
                  {lead.email}
                </td>
                <td className='remove-item'>
                  <Popup
                    trigger={
                      <button type="button" className="button request-removal">
                        <FiTrash size={24} />
                      </button>
                    }
                    position={['bottom center']}
                  >
                    <div className="tooltip">Clique <span className="confirm-removal" onClick={() => handleDelete(lead.id)}>aqui</span> para confirmar a exclusão!</div>
                  </Popup>
                </td>
              </S.LeadRow>))}
          </tbody>
        </S.Table>
      }

      {loading &&
        <S.MessageContainer>
          <Ring size={100} color="#6421ff" />
        </S.MessageContainer>}
    </S.Content>
  </S.Container>);
};

export default ListLead;
