import React, { useEffect, useState, useCallback } from 'react';
import { Ring } from 'react-awesome-spinners';
import { useQueryParam, StringParam } from 'use-query-params';

import { FiTrash } from 'react-icons/fi';
import callie from '../../assets/callie.svg'

import api from '../../services/api';

import { useToast } from '../../hooks/toast';
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';

import Header from '../../components/Header';

import * as S from './styles';

const ListTvShow = () => {
  const [tvshows, setTvShows] = useState([]);
  const [loading, setLoading] = useState(true);
  const [queryTitle, setQueryTitle] = useQueryParam('name', StringParam);
  const { addToast } = useToast();

  const handleSearchSubmit = useCallback(({ name }) => {
    setQueryTitle(name || undefined);
  }, [setQueryTitle]);

  const loadTvShow = useCallback(async () => {
    try {
      setLoading(true);
      const response = await api.get('/tvshows', {
        params: {
          title: queryTitle || undefined,
        },
      });
      setTvShows(response.data.data);
    }
    catch (err) {
      addToast({
        type: 'error',
        title: 'Vixe!',
        description: 'Ocorreu um erro ao tentar buscar registros'
      });
    }
    finally {
      setLoading(false);
    }
  }, [addToast, queryTitle]);

  const handleDelete = async (tvshowId) => {
    api.delete(`/tvshows/${tvshowId}`)
      .then(async (response) => {
        loadTvShow();
        addToast({
          type: 'success',
          title: 'Tudo certo!',
          description: 'Item removido com sucesso'
        });
      });
  };

  useEffect(() => {
    loadTvShow();
  }, [addToast, queryTitle, loadTvShow]);

  return (<S.Container>
    <S.Content>
      <Header initialName={queryTitle} onSubmit={handleSearchSubmit} createPage="/admin/tvshows/register" title="Séries de TV" placeholder="Busque pelo nome" />

      {tvshows.length === 0 && !loading &&
        <S.MessageContainer>
          <span>Nenhum registro encontrado!</span>
        </S.MessageContainer>}

      {tvshows.length > 0 && !loading &&
        <S.Table>
          <tbody>
            {tvshows.map(tvshow => (
              <S.TvShowRow key={tvshow.id}>
                <td>
                  <img src={
                    tvshow.cover ?
                      `http://localhost:3333/uploads/${tvshow.cover}` :
                      callie
                  } alt={tvshow.title} />
                </td>
                <td className="title">
                  {tvshow.title}
                  <small>{tvshow.seasons} {tvshow.seasons === 1 ? 'Temporada' : 'Temporadas'}</small>
                </td>
                <td>
                  {tvshow.overview}
                </td>
                <td>
                  <small>Distribuição:</small>
                  {tvshow.company.name}
                </td>
                <td>
                  <small>Lançamento:</small>
                  {tvshow.release_year}
                </td>
                <td className='remove-item'>
                  <Popup
                    trigger={
                      <button type="button" className="button request-removal">
                        <FiTrash size={24} />
                      </button>
                    }
                    position={['bottom center']}
                  >
                    <div className="tooltip">Clique <span className="confirm-removal" onClick={() => handleDelete(tvshow.id)}>aqui</span> para confirmar a exclusão!</div>
                  </Popup>
                </td>
              </S.TvShowRow>))}
          </tbody>
        </S.Table>
      }

      {loading &&
        <S.MessageContainer>
          <Ring size={100} color="#6421ff" />
        </S.MessageContainer>}
    </S.Content>
  </S.Container>);
};

export default ListTvShow;
